<template>
    <div class="comment">
        <h3>评论</h3>
        <div class="comment_content">
            <textarea v-model="commentParams.content"></textarea>
            <div class="comment_bottom">
                <span>Ctrl+Enter也可以提交哦</span>
                <button @click="postComment()">发表评论</button>
            </div>
            <div class="comment_login" v-if="!this.$store.state.user.uid">
                <div class="comment_login_inner"><a href="/login">登录</a>后参与讨论，发表评论</div>
            </div>
        </div>
        <div class="comment_list" v-if="comments">
            <h4>最新评论（{{comments.length}}条）</h4>
            <ul class="cmt_ul">
                <li v-for="comment in comments" :key='comment.id'>
                    <!-- <a :href="`/home/space-recipe/${comment.userId}`"> -->
                    <router-link :to="`/home/space-recipe/${comment.userId}`" tag="a">
                    <img :src="`/public/uploads/avatar/${comment.avatar}`" alt="">
                    <!-- </a> -->
                    </router-link>
                    <div class="comment_txt">
                        <div class="comment_name">
                            <span>
                                <a :href="`/home/space-recipe/${comment.userId}`">
                                    {{comment.username}}
                                </a>
                            </span>
                            <time>{{comment.updatedAt}}</time>
                            <!-- <a href="#">回复</a> -->
                        </div>
                        <p>
                            {{comment.content}}
                        </p>
                        <!-- <span>来自：<i>Android</i>客户端</span> -->
                    </div>
                </li>
            </ul>
            
        </div>
        <div class="no-text" v-if="tipsBoolean" v-html='tipsTxt'></div>
        <button class="more" @click="handleMoreContent" v-if="!tipsBoolean">查看更多</button>
    </div>
</template>
<script>
import { getCommentApi,postCommentApi } from '@/api/index';
export default {
    data(){
        return{
            comments:[],
            params:{
               cookbookId:this.$route.params.id,
               page:1//显示页数
            },
            commentParams:{
                userId:this.$store.state.user.uid,
                cookbookId:this.$route.params.id,
                content :''

            },
            tipsTxt:'',
            tipsBoolean:false
        }
    },
    mounted(){
        this.getComment();
    },
    methods:{
        async getComment(){
            let res = await getCommentApi(this.params);
            if(res && !res.data.code){
                if(!res.data.comments.length){
                    this.tipsTxt = '暂无最新数据';
                    this.tipsBoolean = true;
                    return;
                }
                this.comments =this.comments.concat(res.data.comments);
            }
        },
        async postComment(){
            //在未登录情况下 转到登录页面 禁止提交
            if(!this.commentParams.userId){
                this.$router.push('/login');
                return
            }
            let res = await postCommentApi(this.commentParams);
            if(res && !res.data.code){
                this.commentParams.content = '';
                this.getComment();
            }
        },
        //加载更多
        handleMoreContent(){
            this.params.page += 1;
            this.getComment(this.params);
        }
    }
    
}
</script>
<style>
.no-text{
    height: 55px;
    line-height: 55px;
    font-size: 22px;
    text-align: center;
    color: #ccc;
}

</style>


