<template>
    <article class="food_details">
        <div class="article_name clearfix">
            <h3>{{cookbook.name}}</h3>
            <ul class="article_info">
                <li>
                    <i>爱心</i>
                    <span>{{cookbook.favoriteCount}}</span>
                </li>
                <li>
                    <i>留言</i>
                    <span>{{cookbook.commentCount}}</span>
                </li>
                <li>
                    <i class="article_img">
                        <router-link 
                            :to="`/home/space-recipe/${cookbook.userId}`"
                            tag="a"
                        >
                        <img :src="`/public/uploads/${user.avatar}`" alt="">
                        </router-link>
                    </i>
                    <span>
                        <router-link 
                            :to="`/home/space-recipe/${cookbook.userId}`"
                            tag="a"
                        >
                            {{user.username}}
                        </router-link>
                    </span>
                </li>
            </ul>
        </div>
        <div class="food_content">
            <div class="food_img">
                <img src="img/download.jpg" alt="">
            </div>
            <p class="food_describe">
                {{cookbook.description}}
            </p>
            <div class="food_detailed">
                <h4 class="food_title">食材明细</h4>
                <fieldset>
                    <legend>主料</legend>
                    <ul class="detailed_list clearfix" v-if="cookbook.ingredients">
                        <li v-for="m of cookbook.ingredients.m" :key="m.id">
                            <span>{{m.k}}</span>
                            <span>{{m.v}}</span>
                        </li>
                       
                    </ul>
                </fieldset>
                <fieldset>
                    <legend>辅料</legend>
                    <ul class="detailed_list clearfix" v-if="cookbook.ingredients">
                        <li v-for="s of cookbook.ingredients.s" :key="s.id">
                            <span>{{s.k}}</span>
                            <span>{{s.v}}</span>
                        </li>
                    </ul>
                </fieldset>
                <div class="detailed_last">
                    <ul class="detailed_list clearfix">
                        <li>
                            <span>工艺</span>
                            <span>{{cookbook.craft}}</span>
                        </li>
                        <li>
                            <span>难度</span>
                            <span>{{cookbook.level}}</span>
                        </li>
                        <li>
                            <span>口味</span>
                            <span>{{cookbook.taste}}</span>
                        </li>
                        <li>
                            <span>耗时</span>
                            <span>{{cookbook.needTime}}</span>
                        </li>
                    </ul>
                </div>
                <div class="food_step">
                    <h4 class="food_title">{{cookbook.name}}的做法步骤</h4>
                    <ol class="step_list">
                        <li v-for="(step,index) in cookbook.steps" :key="step.id">
                            <img :src="`/public/uploads/cookbooks/${step.p}`" alt="">
                            <div class="step_text">
                                <i>{{index+1}}</i>
                                <p>{{step.d}}</p>
                            </div>
                        </li>
                        
                    </ol>
                </div>
                <div class="food_tip">
                    <h4 class="food_title">小窍门</h4>
                    <div class="tip_txt">
                        
                        <p>
                            {{cookbook.tips}}
                        </p>
                        <span class="food_fenlei">
                            所属分类：
                            <a v-for="cooker in cookbook.cookers" :key="cooker.id">
                                {{cooker}}
                            </a>
                        </span>
                    </div>

                </div>
            </div>
        </div>
    </article>
</template>
<script>
import { getViewApi } from '@/api/index';
export default {
    data(){
        return{
            cookbook:{},
            user:{},
            comments:[],
            params:{
               cookbookId:this.$route.params.id
            }
        }
    },
    mounted:function(){
        this.getView();  
    },
    methods:{
        async getView(){
            let res = await getViewApi(this.params);
            if(res && !res.data.code){
                //从后台请求数据赋值给变量
                this.cookbook = Object.assign({}, res.data.cookbook);
                // this.cookbook = res.data.cookbook;
                this.user = res.data.user;
                //使用v-model绑 定数据 接收子组件向父组件传值 语法糖
                this.$emit('input',res.data.currentcategory)
                
            }
        }
        
    }
}
</script>

